.list {
    width: 100%;
    height: 40%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;    
    margin: 0;
    padding: 0;
    border-bottom: 3px solid #2e344e;
  }
  .list li {
    width: 100%;
    height: 50px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    list-style: none;
   
  }
  .list li a {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #a4acc4;
    text-decoration: none;
    text-transform: uppercase;
    outline: none;
    transition: all 0.4s ease-in-out;
  }
  .list li a:hover {
    background-color: #037fff;
  }
  .active {
    color: white;
    background-color: #037fff;
  }